
import { productsService } from '@/services/products-service.js'

export const productsStore = {
  state: {
    products: null,
    cartProducts: [],
    currProduct: null,
    currProductType: null,
    productsToShow: null,
  },
  getters: {
    currProduct(state) {
      return state.currProduct
    },
    products(state) {
      if (state.currProductType) return state.productsToShow
      else return state.products
    },
    cartProducts(state) {
      const cartProducts = state.cartProducts.reduce((acc, prod) => {
        if (!acc[prod._id]) {
          acc[prod._id] = { ...prod, quantity: 0 }
        }
        acc[prod._id].quantity++
        return acc
      }, {})
      return cartProducts
    },
    cartLength(state) {
      return state.cartProducts.length
    },
    cartTotalAmount(state) {
      const cartProducts = state.cartProducts
      const sumProducts = cartProducts.reduce((acc, prod) => {
        acc += prod.price
        return acc
      }, 0)
      return sumProducts
    }
  },
  mutations: {
    setProducts(state, { products }) {
      state.products = products
    },
    removeProduct(state, { productId }) {
      const idx = state.cartProducts.findIndex(
        product => product._id === productId
      )
      state.cartProducts.splice(idx, 1)
    },
    addProduct(state, { productId }) {
      const product = state.products.find(product => product._id === productId)
      state.cartProducts.unshift(product)
    },
    getProductById(state, { productId }) {
      const product = state.products.find(product => product._id === productId)
      state.currProduct = product
    },
    setProductType(state, { productType }) {
      if (productType === 'All' || !productType || !state.products) state.currProductType = null
      else {
        state.currProductType = productType
        const products = state.products.filter(product => product.category.includes(state.currProductType))
        state.productsToShow = products
      }
    }
  },
  actions: {
    async loadProducts({ commit }) {
      const products = await productsService.getProducts()
      commit({ type: 'setProducts', products })
      return products
    },
  }
}
