<template>
  <li v-if="product" class="cart-preview flex space center">
    <div class="img-container flex center wrap">
      <img
        @click.stop="getProductDetail"
        class="product-img"
        :src="require(`@/assets/img/${product.imgName}.png`)"
        alt="product-image"
      />
      <span>
      <h3>{{ product.title }}</h3>
      <h4>€{{ product.price }}</h4>
      </span>
    </div>
    <div class="content-container flex column wrap">
      <span class="flex center">
        <i
          @click.stop="changeQuantity(-1, product._id)"
          class="el-icon-remove-outline pointer"
        ></i>
        {{ product.quantity }}
        <i
          @click.stop="changeQuantity(1, product._id)"
          class="el-icon-circle-plus-outline pointer"
        ></i>
      </span>
    </div>
  </li>
</template>

<script>
export default {
  name: 'cart-preview',
  props: {
    product: Object,
  },
  methods: {
    getProductDetail() {
      this.$router.push(`/product/${this.product._id}`);
    },
    changeQuantity(diff, productId) {
      if (diff === -1) this.$store.commit({ type: 'removeProduct', productId });
      else this.$store.commit({ type: 'addProduct', productId });
    },
  },
};
</script>