<template>
  <div class="home main-layout" v-if="products">
    <product-list :products="products" />
  </div>
</template>

<script>
import ProductList from '@/components/ProductsList.vue';
export default {
  name: 'Home',
  components: {
    ProductList,
  },
  created() {
    let productType = this.$route.params.productType || null;
    this.$store.commit({ type: 'setProductType', productType });
  },
  watch: {
    '$route.params.productType': {
      handler: function (value) {
        let productType = value;
        this.$store.commit({ type: 'setProductType', productType });
      },
      deep: true,
      immediate: true,
    },
  },
  computed: {
    products() {
      return this.$store.getters.products;
    },
  },
};
</script>
