<template>
  <li
    v-if="product"
    class="product-preview pointer col space flex"
    @click="getProductDetail"
  >
    <span class="img-container"> 
      <img
        class="product-img"
        :src="require(`@/assets/img/${product.imgName}.png`)"
        alt="product-image"
      >
      <button class="btn" @click.stop="addToCart(product._id)">
        <i class="el-icon-shopping-cart-full"></i>
      </button>
    </span>
    <span class="desc-container flex space center">
        <h3>{{ product.title }}</h3>
        <p>€{{ product.price }}</p>
    </span>
  </li>
</template>

<script>
export default {
  name: 'ProductPreview',
  props: {
    product: Object,
  },
  created() {},
  methods: {
    addToCart(productId) {
      this.$store.commit({ type: 'addProduct', productId });
    },
    getProductDetail() {
      this.$router.push(`/product/${this.product._id}`);
    },
  },
};
</script>
