<template>
  <section class="product-details main-layout" v-if="product">
    <span class="product-container flex space col center">
    <span>
      <img
        class="product-img"
        :src="require(`@/assets/img/${product.imgName}.png`)"
        alt="product-image"
      />
    </span>
    <span>
      <h3>{{ product.title }}</h3>
      <h4>€{{ product.price }}</h4>
      <button class="btn" @click.stop="addToCart(product._id)">
        <i class="el-icon-shopping-cart-full"></i>
      </button>
    </span>
    </span>
  </section>
</template>

<script>
export default {
  computed: {
    product() {
      return this.$store.getters.currProduct;
    },
  },
  methods: {
    addToCart(productId) {
      this.$store.commit({ type: 'addProduct', productId });
    },
  },
  created() {
    const productId = this.$route.params.productId;
    this.$store.commit({ type: 'getProductById', productId });
  },
};
</script>
