<template>
  <section class="main-layout cart">
    <span
      v-if="cartTotalAmount !== '0.00'"
      class="flex space cart-container wrap"
    >
      <CartList v-if="cartProducts" :cartProducts="cartProducts" />
      <div class="payment flex center col">
      <h3>TOTAL €{{ cartTotalAmount }}</h3>
      <button>Checkout</button>
      <img
        @click.stop="getProductDetail"
        class="payment-img"
        :src="require(`@/assets/img/payment.png`)"
        alt="payment-image"
      />
      </div>
    </span>
    <div v-else class="empty-cart">
      <h1>No products in your cart</h1>
    </div>
  </section>
</template>

<script>
import CartList from '@/components/CartList';
export default {
  name: 'cart',
  components: {
    CartList,
  },
  computed: {
    cartProducts() {
      return this.$store.getters.cartProducts;
    },
    cartTotalAmount() {
      return this.$store.getters.cartTotalAmount.toFixed(2);
    },
  },
};
</script>

